    @import '~@/assets/style/varibles.styl'

    .header-abs
        position: absolute
        left: .2rem
        top: .2rem
        height: .8rem
        width: .8rem
        background: #000
        border-radius: .4rem
        text-align: center

    .header-fixed
        position: fixed
        top: 0
        left: 0
        right: 0
