<template>
    <div class="container" @click="handkeGallaryClick">
        <div class="wrapper">
            <swiper :options="swiperOption">
                <swiper-slide v-for="(item,index) of img">
                    <img class="gallary-img"
                         :src="item"
                        :key="index">
                </swiper-slide>
                <div class="swiper-pagination" slot="pagination"></div>
            </swiper>
        </div>
    </div>
</template>

<script>
    export default {
        name: "CommonGallary",
        props:{
          img:{
              type:Array,
              default(){
                  return []
              }
          }
        },
        methods:{
            handkeGallaryClick(){
                this.$emit('close')
            }
        },
        data() {
            return {
                swiperOption: {
                    pagination: '.swiper-pagination',//轮播分页
                    loop:true, //循环轮播(最后一张左滑到第一张)
                    paginationType : 'fraction',
                    observeParents:true,
                    observer:true,
                },

            }
        }
    }
</script>

<style lang='stylus' scoped>
    .container>>>.swiper-container
        overflow :inherit
    .container
        z-index:999
        display: flex
        flex-direction: column
        justify-content: center
        position: fixed
        top: 0
        left: 0
        right: 0
        bottom: 0
        background: #2b2b2b
     .wrapper
         width:100%
         height:100vw
      .swiper-pagination
          color:#fff
          bottom:-1rem
    .gallary-img
            width: 100%
            height:100vw
</style>
